function intProjectSliders() {
    projectSlider()
    projectSlider2()
    projectSlider3()
    projectSlider4()
    projectSlider5()
    projectSlider6()
}

$(document).ready(function() {
    mainSlider()
    sponsorSlider()
    serviceSlider()
    productSlider()
    partnersSlider()
    tabSlider()
    tabSlider2()
    intProjectSliders()


    $(".header__burger").click(function() {
        $(this).toggleClass("header__burger--active")
        $(".nav").toggleClass("nav--active")
        $("body").toggleClass("fixed-body")
    })

    if (window.innerWidth < 768) {
        $(".header__contacts").insertAfter(".nav__item:last-child")
    }

    if (window.scrollY > 80) {
        $(".header").addClass("header--active")
    } else {
        $(".header").removeClass("header--active")
    }

    $(".product__slider-item").click(function() {
        let path = $(this).attr("data-tab-path")
        $(".product__slider-item").removeClass("product__slider-item--active")
        $(this).addClass("product__slider-item--active")
        $(".product__info").removeClass("product__info--active")
        $(`.product__info[data-tab-path="${path}"]`).addClass("product__info--active")
    })

    $(".tab-slider__item").click(function() {
        let path = $(this).attr("data-tab-path")
        let parent = $(this).parents(".tab-block")
        $(".tab-slider__item").removeClass("tab-slider__item--active")
        $(this).addClass("tab-slider__item--active")
        $(parent).find(".tab-slider__content").removeClass("tab-slider__content--active")
        $(parent).find(`.tab-slider__content[data-tab-path="${path}"]`).addClass("tab-slider__content--active")
    })

    $(".project-card").click(function() {
        $(this).siblings(".project-card").removeClass("project-card--active")
        $(this).addClass("project-card--active")
    })

    $(".projects__tabs .tab-slider__item").click(function() {
        intProjectSliders()
    })

    if (window.innerWidth < 992) {
        $(".project-card").addClass("project-card--mob")
        $(".news__item--big-mob").removeClass("news__item--big")
    } else {
        $(".project-card").removeClass("project-card--mob")
        $(".news__item--big-mob").addClass("news__item--big")
    }


    $(".contacts-cities__item").click(function() {
        let path = $(this).attr("data-tab-path")
        $(".contacts-cities__item").removeClass("contacts-cities__item--active")
        $(this).addClass("contacts-cities__item--active")
        $(".contacts-info__item").removeClass("contacts-info__item--active")
        $(`.contacts-info__item[data-tab-path="${path}"]`).addClass("contacts-info__item--active")
    })

})

$(window).resize(function() {
    if (window.innerWidth < 992) {
        $(".project-card").addClass("project-card--mob")
        $(".news__item--big-mob").removeClass("news__item--big")
    } else {
        $(".project-card").removeClass("project-card--mob")
        $(".news__item--big-mob").addClass("news__item--big")
    }
})

$(window).resize(function() {
    if (window.innerWidth < 768) {
        $(".header__contacts").insertAfter(".nav__item:last-child")
    } else {
        $(".header__contacts").insertAfter(".nav")
    }
})


$(window).scroll(function() {
    if (window.scrollY > 80) {
        $(".header").addClass("header--active")
    } else {
        $(".header").removeClass("header--active")
    }
})